<template>
	<page-meta>
		<navigation-bar
			:title="nbTitle"
			:front-color="nbFrontColor"
			:background-color="nbBackgroundColor"
			color-animation-duration="2000"
			color-animation-timing-func="easeIn"
		></navigation-bar>
	</page-meta>
	<view>
		<view style="display:flex; flex-direction:column; min-height: 150px; padding: 10px; margin-bottom: 70px;">
			<block v-for="(item, index) in contents" :key="index">
				<view v-if="item.sender == identity">
					<view>
						<view class="avatar-send"><image class="avatar-size" :src="myProfile.avatarUrl"></image></view>
						<view class="content-info-send">{{ myProfile.nickName }} {{ item.create_time }}</view>
						<view class="chat-my-send">{{ item.content }}</view>
					</view>
				</view>
				<view v-else>
					<view class="avatar-receive"><image class="avatar-size" :src="otherProfile.avatarUrl"></image></view>
					<view class="content-info-receive">{{ item.create_time }} {{ otherProfile.nickName }}</view>
					<view class="chat-my-receive">{{ item.content }}</view>
				</view>
			</block>
		</view>
		<view> class="divLine-bottom"</view>
		<view class="bottom">
			<view class="weui-message-bar">
				<view class="weui-message-bar__form">
					<view class="weui-message-bar__box">
						<input
							style="width: 708rpx; height: 78rpx; display: block; box-sizing: border-box; left: NaNrpx; top: NaNrpx"
							hold-keyboard="true"
							class="weui-message-bar__input"
							:value="content"
							@confirm="sendMessage"
							confirm-type="send"
							@input="getMessage"
						/>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
// pages/message/chat/chat.js
const app = getApp();

export default {
	data() {
		return {
			nbFrontColor: '#000000',
			nbBackgroundColor: '#fae4fa',
			postid: '',
			chatid: '',
			identity: 'initiator',
			content: '',
			contents: [],
			myProfile: {
				avatarUrl: '',
				nickName: ''
			},
			otherProfile: {
				avatarUrl: '',
				nickName: ''
			},
			test: [],
			chatSocket: null,
			nbTitle: '',
			nbLoading: false
		};
	},

	components: {},
	props: {},

	/**
	 * 生命周期函数--监听页面加载
	 */
	onLoad: function(options) {
		var postid = options.postid;
		var chatid = options.chatid;
		this.setData({
			nbTitle: '聊天',
			nbLoading: false,
			postid: options.postid,
			chatid: options.chatid
		});
	},
	onShow: function() {
		app.globalData.checkSession_3rd();
		this.getAccount();
	},
	onUnload: function() {
		this.chatSocket.close();
	},
	methods: {
		createWebSocket: function() {
			var that = this;
			this.chatSocket = uni.connectSocket({
				url: app.globalData.socket + '/wx/chat/' + this.chatid,
				success: () => {}
			});
			this.chatSocket.onOpen(
				this.chatSocket.onMessage(function(res) {
					console.log(res.data);
					var content = JSON.parse(res.data);

					if (content.sender != this.identity) {
						content.isRead = true;
						that.isRead(content.contentid);
					}

					that.contents = that.contents.concat(content);
					that.setData({
						contents: that.contents
					});
					that.srcollTo();
				})
			);

			// this.chatSocket.onSocketMessage(res => {
			// 	console.log(res.data);
			// 	var content = JSON.parse(res.data);

			// 	if (content.sender != this.identity) {
			// 		content.isRead = true;
			// 		this.isRead(content.contentid);
			// 	}

			// 	that.contents = that.contents.concat(content);
			// 	that.setData({
			// 		contents: that.contents
			// 	});
			// 	that.srcollTo();
			// });
		},
		getAccount: function() {
			var chatid = this.chatid;
			var postid = this.postid;

			if (chatid) {
				this.checkIdentity();
				this.createWebSocket();
				this.getOtherProfileByChatID();
				this.getPrevContents();
			}

			if (postid) {
				this.createChat();
				this.getOtherProfileByPostID();
			}
		},
		checkIdentity: function() {
			var that = this;
			uni.request({
				url: app.globalData.domain + '/wx/chat/checkIdentity',
				data: {
					chatid: that.chatid,
					session_3rd: app.globalData.session_3rd
				},
				success: request_res => {
					that.setData({
						identity: request_res.data
					});
				}
			});
		},
		getOtherProfileByPostID: function() {
			var that = this;
			var postid = this.postid;
			uni.request({
				url: app.globalData.domain + '/wx/account/getOtherProfileByPostID',
				data: {
					session_3rd: app.globalData.session_3rd,
					postid: postid
				},
				success: request_res => {
					that.setData({
						myProfile: request_res.data[0],
						otherProfile: request_res.data[1],
						nbTitle: request_res.data[1].nickName,
					});
				}
			});
		},
		getOtherProfileByChatID: function() {
			var that = this;
			var chatid = this.chatid;
			uni.request({
				url: app.globalData.domain + '/wx/account/getOtherProfileByChatID',
				data: {
					session_3rd: app.globalData.session_3rd,
					chatid: chatid
				},
				success: request_res => {
					that.setData({
						myProfile: request_res.data[0],
						otherProfile: request_res.data[1],
						nbTitle: request_res.data[1].nickName,
					});
				}
			});
		},
		createChat: function() {
			var that = this;
			var postid = this.postid;
			uni.request({
				url: app.globalData.domain + '/wx/chat/createChat',
				data: {
					session_3rd: app.globalData.session_3rd,
					postid: postid
				},
				success: request_res => {
					that.setData({
						chatid: request_res.data
					});
					that.checkIdentity();
					that.createWebSocket();
					that.getPrevContents();
				}
			});
		},
		getMessage: function(e) {
			this.setData({
				content: e.detail.value
			});
		},
		sendMessage: function() {
			var that = this;
			this.chatSocket.onOpen(
				this.chatSocket.send({
					data: app.globalData.session_3rd + ' ' + that.content
				})
			);
			// that.chatSocket.send({
			// 	data: app.globalData.session_3rd + ' ' + that.content
			// });
			// wx.sendSocketMessage({
			//   data:app.session_3rd+" "+that.data.content
			// })

			this.setData({
				content: ''
			});
		},
		getPrevContents: function() {
			var that = this;
			uni.request({
				url: app.globalData.domain + '/wx/chat/getPrevContents',
				data: {
					chatid: that.chatid
				},
				success: request_res => {
					console.log(request_res.data);
					var contents = request_res.data;

					for (var i = 0; i < contents.length; i++) {
						if (contents[i].sender != that.identity) {
							contents[i].isRead = true;
							this.isRead(contents[i].contentid);
							console.log('read');
						}
					}

					that.setData({
						contents: contents
					});
					that.srcollToStart();
				}
			});
		},
		srcollToStart: function() {
			setTimeout(() => {
				uni.pageScrollTo({ scrollTop: this.contents.length * 1000, duration: 0 });
			}, 0);
		},
		srcollTo: function() {
			setTimeout(() => {
				uni.pageScrollTo({ scrollTop: this.contents.length * 1000, duration: 300 });
			}, 0);
		},
		isRead: function(contentid) {
			uni.request({
				url: app.globalData.domain + '/wx/chat/isRead',
				data: {
					contentid: contentid
				}
			});
		}
	}
};
</script>
<style>
/* pages/message/chat/chat.wxss */

.weui-message-bar {
	position: relative;
	padding: 20rpx 20rpx 20rpx 20rpx;
	display: -webkit-box;
	display: -webkit-flex;
	display: flex;
	box-sizing: border-box;
	background-color: #eeeeee;
}

.weui-message-bar__form {
	bottom: 0rpx;
	position: relative;
	-webkit-box-flex: 1;
	-webkit-flex: auto;
	flex: auto;
	border-radius: 5rpx;
	background: #ffffff;
	border: 1rpx solid #ffffff;
}

.weui-message-bar__input {
	bottom: 0rpx;
	height: 35rpx;
	line-height: 35rpx;
	font-size: 30rpx;
}

.bottom {
	position: fixed;
	margin: 100rpx, 0rpx, 0rpx, 0rpx;
	bottom: 0rpx;
	width: 100%;
	height: 150rpx;
	background-color: #eeeeee;
}

.message {
	display: flex;
	flex-direction: row;
}

.content-container {
	margin-bottom: 100rpx;
}

.chat-my-receive {
	clear: both;
	position: relative;
	float: left;
	padding: 0rpx;
	color: #848484;
	background-color: #ffffff;
	margin: 50rpx 200rpx 20rpx 110rpx;
	padding: 20rpx 20rpx 20rpx 20rpx;
	border-radius: 7rpx;
	word-wrap: break-word;
	word-break: break-all;
	text-align: left;
}

.chat-my-receive:after {
	position: absolute;
	content: '';
	width: 0rpx;
	height: 0rpx;
	border-width: 10rpx 10rpx;
	border-style: solid;
	border-color: transparent #ffffff transparent transparent;
	top: 20rpx;
	right: 100%;
	margin-right: 0rpx;
}
.chat-my-send {
	clear: both;
	position: relative;
	float: right;
	padding: 0rpx;
	color: #848484;
	background-color: #b2e281;
	margin: 50rpx 110rpx 20rpx 200rpx;
	padding: 20rpx 20rpx 20rpx 20rpx;
	border-radius: 7rpx;
	word-wrap: break-word;
	word-break: break-all;
	text-align: left;
}

.chat-my-send:after {
	position: absolute;
	content: '';
	width: 0rpx;
	height: 0rpx;
	border-width: 10rpx 10rpx;
	border-style: solid;
	border-color: transparent transparent transparent #b2e281;
	top: 20rpx;
	left: 100%;
	margin-left: 0rpx;
}

.chat-notice {
	clear: both;
	font-size: 70%;
	color: white;
	text-align: center;
	margin-top: 15rpx;
	margin-bottom: 15rpx;
}
.chat-notice {
	background-color: #cecece;
	line-height: 25rpx;
	border-radius: 5rpx;
	padding: 5rpx 10rpx;
}

.avatar-size {
	width: 90rpx;
	height: 90rpx;
}

.avatar-send {
	position: relative;
	margin: 20rpx 10rpx -120rpx 200rpx;
	float: right;
}

.avatar-receive {
	position: relative;
	margin: 20rpx 200rpx -120rpx 10rpx;
	float: left;
}

.content-info-send {
	float: right;
	font-size: 20rpx;
	margin: 20rpx 110rpx -120rpx 10rpx;
}

.content-info-receive {
	float: left;
	font-size: 20rpx;
	margin: 20rpx 10rpx -120rpx 110rpx;
}

/*# sourceMappingURL=data:application/json;base64, */
</style>
